import React from 'react';
import { motion } from 'framer-motion';
import Image from 'next/image';
import dia from './diagustive.webp';

const DigestiveSystem = () => {
  const organs = [
    {
      id: 'mouth',
      name: 'Mouth & Esophagus',
      description: 'Start of digestion - Chewing, enzyme release, and food passage.',
      position: { top: '20%', left: '50%' }, // Adjusted to center
      color: '#FF6B6B',
      side: 'left'
    },
    {
      id: 'stomach',
      name: 'Stomach',
      description: 'Food breakdown using acid and enzymes. Key for protein digestion.',
      position: { top: '42%', left: '52%' }, // Adjusted to match stomach position
      color: '#2A6177',
      side: 'right'
    },
    {
      id: 'smallIntestine',
      name: 'Small Intestine',
      description: 'Nutrient absorption powerhouse. 90% of digestion happens here.',
      position: { top: '58%', left: '48%' }, // Adjusted to center
      color: '#4ECDC4',
      side: 'left'
    },
    {
      id: 'largeIntestine',
      name: 'Large Intestine',
      description: 'Water absorption and final processing. Important for gut health.',
      position: { top: '72%', left: '50%' }, // Adjusted to center bottom
      color: '#FF7F50',
      side: 'right'
    }
  ];

  return (
    <div className="relative w-full min-h-screen bg-gradient-to-b from-white to-[#8de8f825] py-16">
      <div className="w-full max-w-[1920px] mx-auto px-4 md:px-8">
        <h2 className="text-3xl md:text-4xl font-bold text-center text-[#2A6177] mb-4">
          Interactive Digestive System Guide
        </h2>
        <p className="text-center text-gray-600 max-w-2xl mx-auto mb-12">
          Explore how your digestive system works by hovering over different points
        </p>

        {/* Updated container size */}
        <div className="relative w-full h-[90vh] bg-white rounded-2xl shadow-lg p-8">
          {/* Adjusted image container */}
          <div className="absolute inset-0 flex items-center justify-center">
            <div className="relative w-[60%] h-[95%]"> {/* Adjusted width and height */}
              <Image
                src={dia}
                alt="Digestive System"
                className="object-contain"
                fill
                priority
              />
            </div>
          </div>

          {/* Interactive Points - Adjusted for full width */}
          {organs.map((organ) => (
            <div
              key={organ.id}
              className="absolute z-20"
              style={{
                top: organ.position.top,
                left: organ.position.left,
              }}
            >
              {/* Pulsing Dot - Made larger */}
              <motion.div
                className="relative"
                initial="initial"
                animate="animate"
              >
                {/* Outer pulse effect */}
                <motion.div
                  className="absolute -inset-6 rounded-full"
                  style={{ backgroundColor: organ.color }}
                  animate={{
                    opacity: [0.2, 0.1, 0.2],
                    scale: [1, 1.5, 1],
                  }}
                  transition={{
                    duration: 2,
                    repeat: Infinity,
                    ease: "easeInOut"
                  }}
                />

                {/* Main dot - Larger size */}
                <div
                  className="w-12 h-12 rounded-full relative z-30"
                  style={{
                    backgroundColor: organ.color,
                    boxShadow: `0 0 30px ${organ.color}40`
                  }}
                >
                  <motion.div
                    className="absolute inset-2 rounded-full bg-white/50"
                    animate={{
                      scale: [1, 1.2, 1],
                      opacity: [0.5, 0.8, 0.5],
                    }}
                    transition={{
                      duration: 1.5,
                      repeat: Infinity,
                      ease: "easeInOut"
                    }}
                  />
                </div>
              </motion.div>

              {/* Info Box - Wider and more prominent */}
              <div
                className={`absolute p-6 bg-white rounded-lg shadow-xl border-l-4
                  ${organ.side === 'left' ? '-left-[400px]' : '-right-[400px]'} 
                  top-1/2 -translate-y-1/2 w-[380px] z-40`}
                style={{
                  borderLeftColor: organ.color,
                  boxShadow: `0 4px 30px ${organ.color}30`
                }}
              >
                <h3 className="text-xl font-bold mb-3" style={{ color: organ.color }}>
                  {organ.name}
                </h3>
                <p className="text-base text-gray-600 leading-relaxed">
                  {organ.description}
                </p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default DigestiveSystem;